import React, { useState, useEffect } from "react";
import styles from "./Home.module.css";
import backgroundImage from "../../../src/components/homePage/Baggrund.png";

export function TextField({ value }) {
  return <input type="text" value={value} readOnly />;
}

export default function Values() {
  const [CO2, setCO2] = useState("");
  const [humidity, setHumidity] = useState("");
  const [temp, setTemp] = useState("");

  //Her fetcher vi data fra vores API
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(
          "https://cors-anywhere.herokuapp.com/http://140.82.33.21:5001/Config/GetConfig"
        );
        if (response.ok) {
          const data = await response.json();
          console.log(data); // Tilfjet konsoludskrift af data
          setTemp(`${data.value[0].temperature} C`);
          setCO2(`${data.value[0].co2} ppm`);
          setHumidity(`${data.value[0].humidity} %`);
        } else {
          console.log("Fejl ved hentning af data fra API'en.");
        }
      } catch (error) {
        console.log("Fejl ved hentning af data fra API'en:", error);
      }
    };

    // Hent data ved komponentens start og derefter hvert 5. minut
    fetchData();
    const interval = setInterval(fetchData, 300000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div
      className={styles.baggrund}
      style={{ backgroundImage: `url(${backgroundImage})` }}
    >
      <div className={styles.overskrift}>
        <h1>DRIVHUS</h1>
      </div>

      <div className={styles.displayBox1}>
        <h2>Temperatur</h2>
        <div className={styles.displayTemp}>{temp}</div>
      </div>

      <div className={styles.displayBox2}>
        <h2>CO2</h2>
        <div className={styles.displayCO2}>{CO2}</div>
      </div>

      <div className={styles.displayBox3}>
        <h2>Luftfugtighed</h2>
        <div className={styles.displayLuft}>{humidity}</div>
      </div>
    </div>
  );
}
